<template lang="pug">
section(class="services")
    div(class="container")
        div(class="title")
            h2 {{ $t("our-services") }}
            p {{ ServicesData.servicesDesc }}
        div(class="services-content")
            div(class="row")
                div(class="col-md-6 col-lg-3 box" v-for="service in ServicesData.services", :key="service.id")
                    router-link(:to="`/services/${service.url}`")
                        div(class="services-box wow fadeInUp" data-wow-duration="1s")
                            div(class="services-box-icon")
                                img(:src="service.image" class="img-fluid" alt="")
                            div(class="services-box-details")
                                h3 {{ service.title }}
                                p {{ service.body }}
</template>

<script>
import { mapState } from "vuex";

export default {
    name: "Services",
    metaInfo() {
        return {
            title: process.env.VUE_APP_TITLE,
            titleTemplate: `%s | ${this.ServicesData.website_title}`,
            meta: [
                { charset: "utf-8" },
                {
                    name: "description",
                    content: this.ServicesData.meta_description,
                },
                {
                    name: "keywords",
                    content: this.ServicesData.meta_keywords,
                },
            ],
        };
    },
    mounted() {
        this.$store.dispatch("getServicesData");
    },
    computed: {
        ...mapState(["ServicesData"]),
    },
};
</script>
